    padding: 0px;
}

body{
    display: flex;
    flex-direction: column;
    height: 100%;
    margin: 0px;
    position: relative;
}

header{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15px;
    padding: 10px 0px;
    font-size: clamp(1rem, 0.893rem + 0.536vw, 1.375vw); ;
}

#searchForm{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15px;
}

#search{
    padding: 4px 10px;
    border-radius: 10px;
}

#searchBtn{
    padding: 5px 10px;
    border-radius: 20px;
}

main{
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 10px 2vw 10px 4vw;
}

#currentWeather{
    display: flex;
    justify-content: space-between;
}

#top{
    width: min(600px,70vw);
    display: flex;
    flex-direction: column;
    gap: 20px;
}

#mainInfo{
    flex: 1;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 70px;
    gap: 10px;
    justify-content: center;
    align-items: center;
}

.conditionCon{
    display: flex;
    align-items: center;
}

.conditionCon p{
    margin: 0px;
}

.asideIcon{
    height: 50px;
    width: auto;
}

#sideInfo{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding-right: 10px;
}

#detalles{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    gap: 10px 5px;
}

#currentWeather p{
    margin: 0px;
    font-size: clamp(1rem, 0.893rem + 0.536vw, 1.375vw); 
}

#unidadContainer label{
    font-size: clamp(1rem, 0.893rem + 0.536vw, 1.375vw); 
}

#time{
    font-size: 24px;
}

#forecastContainer{
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(230px,1fr));
    gap: 15px;
}

.dayCard{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 5px 10px;
}

.dayCard p{
    font-size: clamp(1rem, 0.893rem + 0.536vw, 1.375vw); 
}

.dayCardNight{
    background-color: darkcyan;
}

.dayCardSunny{
    background-color: bisque;
}

footer{
    display: flex;
    justify-content: space-around;
}

.sunny{
    /* Specify the URL of the image */
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-repeat: repeat; 
    /* background-position: center center; */
    color: black;
}

.night{
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
    background-repeat: repeat; 
    /* background-position: center center; */
    color: white;
}

/*Hour forecast container*/

#hourForecast{
    width: 0px; 
    height: fit-content;
    position: absolute;
    top: 0px;
    right: 0px;
    overflow: hidden;
    word-wrap: none;
    transition: width 0.5s  ease-in-out;
}

#hourForecastTop{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    padding: 10px 10vw;
    align-items: center;
    gap: 10px;
    font-size: clamp(1rem, 0.893rem + 0.536vw, 1.375vw); 
}

#hourForecastTop p{
    margin:0px;
}

#hourDate{
    grid-column: 1/3;
    font-size: 24px;
}

#hourForecast.visible{
    width:100%; 
}

.time{
    font-size: 24px;
}

.container{
    width: 100%;
    padding-left: 5vw;
}

#forecastDescription{
    font-size: 20px;
}

#return{
    margin-top: 10px;
    padding: 2px 5px;
}

#hourContainer{
    display: flex;
    flex-direction: column;
    padding: 20px 15vw;
    gap: 20px;
}

.hourContainer{
    display: grid;
    grid-template-columns: repeat(3,auto);
    grid-template-rows: auto auto;
    gap: 10px;
    align-items: center;
    border: 2px solid black;
    padding: 2vh 10vw 2vh 4vw;
}

.hourContainer p{
    margin: 0px;
    font-size: clamp(1rem, 0.893rem + 0.536vw, 1.375vw); 
}

/*Media queries*/
/*Media query para cambiar de horizontal a vertical la presentacion*/
@media (max-width: 650px) {
    #currentWeather{
        flex-direction: column;
        gap: 20px;
    }

    #top{
        width: 100%;
    }

    #sideInfo{
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr 1fr;
        gap: 20px;
    }
  }

@media (max-width:500px) {
    #forecastContainer{
        justify-items: center;
    }

    .hourContainer{
        grid-template-columns: repeat(2,1fr);
    }

    .hourCondition{
        grid-row: 2/3;
        grid-column: 1/3;
    }
    
}

/*Media query final para dispositivos menor a <=400px*/
@media (max-width:400px){
    #sideInfo{
        display: grid;
        grid-template-columns: 1fr;
        grid-auto-rows: 1fr;
        padding-left: 10vw;
    }

    #detalles{
        grid-template-columns: 1fr;
        grid-auto-rows: 1fr;
        padding-left: 10vw;
    }
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,YAAY;IACZ,WAAW;IACX,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,SAAS;IACT,iBAAiB;IACjB,mDAAmD;AACvD;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,SAAS;AACb;;AAEA;IACI,iBAAiB;IACjB,mBAAmB;AACvB;;AAEA;IACI,iBAAiB;IACjB,mBAAmB;AACvB;;AAEA;IACI,OAAO;IACP,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,0BAA0B;AAC9B;;AAEA;IACI,aAAa;IACb,8BAA8B;AAClC;;AAEA;IACI,sBAAsB;IACtB,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,OAAO;IACP,aAAa;IACb,8BAA8B;IAC9B,4BAA4B;IAC5B,SAAS;IACT,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,YAAY;IACZ,WAAW;AACf;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,6BAA6B;IAC7B,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,2BAA2B;IAC3B,aAAa;AACjB;;AAEA;IACI,WAAW;IACX,mDAAmD;AACvD;;AAEA;IACI,mDAAmD;AACvD;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,0DAA0D;IAC1D,SAAS;AACb;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,iBAAiB;AACrB;;AAEA;IACI,mDAAmD;AACvD;;AAEA;IACI,0BAA0B;AAC9B;;AAEA;IACI,wBAAwB;AAC5B;;AAEA;IACI,aAAa;IACb,6BAA6B;AACjC;;AAEA;IACI,iCAAiC;IACjC,yDAAwC;IACxC,yBAAyB;IACzB,wCAAwC;IACxC,YAAY;AAChB;;AAEA;IACI,yDAAwC;IACxC,yBAAyB;IACzB,wCAAwC;IACxC,YAAY;AAChB;;AAEA,0BAA0B;;AAE1B;IACI,UAAU;IACV,mBAAmB;IACnB,kBAAkB;IAClB,QAAQ;IACR,UAAU;IACV,gBAAgB;IAChB,eAAe;IACf,mCAAmC;AACvC;;AAEA;IACI,aAAa;IACb,oCAAoC;IACpC,kBAAkB;IAClB,mBAAmB;IACnB,SAAS;IACT,mDAAmD;AACvD;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,gBAAgB;IAChB,eAAe;AACnB;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,WAAW;IACX,iBAAiB;AACrB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,gBAAgB;IAChB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,kBAAkB;IAClB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,qCAAqC;IACrC,6BAA6B;IAC7B,SAAS;IACT,mBAAmB;IACnB,uBAAuB;IACvB,yBAAyB;AAC7B;;AAEA;IACI,WAAW;IACX,mDAAmD;AACvD;;AAEA,gBAAgB;AAChB,oEAAoE;AACpE;IACI;QACI,sBAAsB;QACtB,SAAS;IACb;;IAEA;QACI,WAAW;IACf;;IAEA;QACI,aAAa;QACb,8BAA8B;QAC9B,2BAA2B;QAC3B,SAAS;IACb;EACF;;AAEF;IA